/**
 * REDUCERS
 *
 * Ya que no podemos modificar el estado directamente
 * (tiene que ser a travs de acciones) y el estado est almacenado
 * en un nico Store (Store Global), para especificar como realizar los
 * cambios en el rbol del estado, utilizamos funciones puras llamadas reducers.
 *
 * Una funcin pura es simplemente una funcin que ante
 * los mismos datos de entrada devuelve el mismo resultado.
 * Es decir, la siguiente funcin de ejemplo sera una funcin pura:
 *
 * function suma (a, b) {
 *     return a + b;
 * }
 *
 * Esta funcin para los mismos parmetros, devuelve siempre lo mismo.
 * Si llamamos a suma(1, 2) siempre devolver 3.
 * De sta manera es ms sencillo depurar y encontrar errores y es ms fcil testear.
 *
 * El reducer es simplemente eso, una funcin que recibe dos parmetros,
 * el "estado inicial" y una "accin" y dependiendo del tipo de accin realizar
 * una operacin u otra en el estado.
 * Siempre de manera inmutable, no podemos modificar el estado,
 * sino crear una copia a partir del anterior. De esta forma es ms fcil
 * rastrear posibles errores.
 *
 * Ejemplo de reducer:
 * function reducer (state, action) {
 *     switch(action.type) {
 *         case 'ADD_ITEM':
 *             return state.concat(action.item);
 *         case 'REMOVE_ITEM':
 *             ...
 *         default:
 *             return state;
 *     }
 * }
 *
 */

// Constante que representan los tipos de acciones
import {
    SHOW_ALERT,
    HIDE_ALERT
} from './actionTypes';

export * from './actions';

/**
 * Props de la modal
 * @param {Array} state Props de la modal
 * @param {String} action Tipo de accin
 */
function alert(state = null, action) {

    switch (action.type) {
        case SHOW_ALERT:
            return action.payload
        case HIDE_ALERT:
            return null;
        default:
            return state;
    }
}

export default alert;